import React, { PureComponent } from "react";
// ! PureComponent is to avoid Rerender for alredy renderd user, when media stream list updated
import { AiFillLike } from "react-icons/ai";
import { IoIosHand, IoIosHeart } from "react-icons/io";
import { RiMicFill, RiMicOffFill } from "react-icons/ri";

import { checkMicSensitivity } from "antsdk";
import posterImg from "../../Image/user.png";
import axios from "axios";


export default class RemoteVideo extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      streamId: null,
      userName: null,
      mediaStream: new MediaStream(),
    }
  }

  componentDidMount = async () => {
    const { streamId, videoTrack } = this.props;
    let updateStream = this.state.mediaStream;
    let index = streamId.replace("ARDAMSv", "");
    let audioTrack = window.webRTCAdaptor.multiStream.getTrackById('ARDAMSa' + index);
    if (audioTrack) {
      updateStream.addTrack(audioTrack);
      updateStream.addTrack(videoTrack);
    }
    await this.setState({ mediaStream: updateStream, streamId: index });
    let u = await axios.get(`https://prematix.tech/WebRTCAppEE/rest/v2/broadcasts/${index}`);
    let userDict = JSON.parse(u.data.name);
    let Name = userDict.hasOwnProperty('username') ? userDict.username : null;
    await this.setState({ userName: Name });
    this.props.notifyUser(`${this.state.userName} Joined ...`);
    if (this.props.ledger.handRiseEvents.hasOwnProperty(this.state.streamId)) {
      this.props.raiseIfRequired("handRaise", this.props.ledger.handRiseEvents[this.state.streamId]);
    };
    await checkMicSensitivity(this.state.mediaStream, this.state.streamId);
  };
  render() {
    const { streamId, userName, mediaStream } = this.state;
    return (
      <div className="antsdk-remote-video-data" id={`player${streamId}`} onContextMenu={(e) => e.preventDefault()}>
        <div className="antsdk-remote-video-content">
          <video className="antsdk-remote-video-tag" ref={(video) => (video ? (video.srcObject = mediaStream) : video)} poster={posterImg} id={`remoteVideo${streamId}`} autoPlay playsInline></video>
          <div className="antsdk-remote-video-speaker" id={`remoteSpeaker${streamId}`}>
            {/* //* speaker highlighter  */}
          </div>
          {/* <meter id={`micSensitivityMeter${streamId}`} max="0.30" high="0.10" min="0.01" value="0"></meter> */}
          <div className="antsdk-remote-video-user-details">
            <span title={`${userName}`} className="antsdk-remote-video-username" id={`userName${streamId}`}>
              {userName || "Guest"}
            </span>
            <i className="antsdk-remote-video-mic-icon antsdk-remote-video-mic-icon-unmute" id={`munmute${streamId}`} style={{ display: "block" }}>
              <RiMicFill />
            </i>
            <i className="antsdk-remote-video-mic-icon antsdk-remote-video-mic-icon-mute" id={`mmute${streamId}`} style={{ display: "none" }}>
              <RiMicOffFill />
            </i>
            <i className="antsdk-remote-video-handraise-icon antsdk-remote-video-handraise-icon-handraise" id={`Hand${streamId}`} style={{ display: "none" }}>
              <IoIosHand
                style={{
                  color: "#ffc800",
                }}
              />
            </i>
            <i className="antsdk-remote-video-handraise-icon antsdk-remote-video-handraise-icon-thumbsup" id={`Thumbsup${streamId}`} style={{ display: "none" }}>
              <AiFillLike
                className="ml-2"
                style={{
                  color: "#ffc800",
                }}
              />
            </i>
            <i className="antsdk-remote-video-handraise-icon antsdk-remote-video-handraise-icon-heart" id={`Heart${streamId}`} style={{ display: "none" }}>
              <IoIosHeart
                style={{
                  color: "#ff0000",
                }}
              />
            </i>
          </div>
        </div>
      </div>
    );
  }
}
