        return (
            <div className="end-call">
                
                      <div className="d-flex  flex-column justify-content-center w-100 mt-5">

        <div className=" d-flex  pt-4 justify-content-center ">
          <img src={endCall} width="300px" alt="Call Ended" />
        </div>
        <div className=" pt-3  text-center" style={{fontSize:"24px",fontWeight:"600", color: "#f14c4c",fontFamily:"Poppins"}}>
Call Ended...
        </div>
        </div>
            </div>
        )
