import React, { Component } from "react";
import "./app.css";
import "bootstrap/dist/css/bootstrap.min.css";
import { SubDirectory } from "./config.json";
import VideoConsult from "./VideoConsultation/VideoCall/VideoCalls/VideoConsult";
import { BrowserRouter as Router, Switch, Route, Redirect } from "react-router-dom";
import VideoHome from "./VideoConsultation/VideoCall/VideoHome/VideoHome";
import EndCall from "./VideoConsultation/VideoCall/EndCall/EndCall";

export default class App extends Component {
  render() {
    return (
      <>
        <Router>
          <Switch>
            <Route path={`${SubDirectory}/VideoConsultation`} component={VideoConsult} />
            <Route path={`${SubDirectory}/videoHome`} component={VideoHome} />
            <Route path={`${SubDirectory}/videoCallEnd`} component={EndCall} />

            <Redirect to={`${SubDirectory}/videoHome`} />
          </Switch>
        </Router>
      </>
    );
  }
}
