import React, { Component } from "react";
import { AiOutlineClose } from "react-icons/ai";
import "./ParticipantList.scss";
export default class ParticipantList extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    const { MediaStreamList } = this.props;
    return (
      <div className="participant-list-all">
        <h4 className="participant-heading">
          {MediaStreamList ? Object.keys(MediaStreamList)?.length + 1 : 0} Participants
          <div className="participant-close" onClick={() => this.props.onToogleParticipant()}>
            <AiOutlineClose />
          </div>
        </h4>
        <div className="list-wrapper">
          <ul className="list">
            {/*//* Local user name */}
            {window.UserName && (
              <li className="list-item">
                <div className="user-name-logo">
                  <div className="user-name-letter">{window.UserName[0]}</div>
                </div>
                <div className="list-item-content">
                  <h4 className="list-name">{window.UserName}</h4>
                </div>
              </li>
            )}
            {/* //* Remote User Names */}
            {MediaStreamList &&
              Object.values(MediaStreamList)?.map((user, i) => {
                return (
                  <>
                    <li className="list-item" key={i}>
                      <div className="user-name-logo">
                        <div className="user-name-letter">{user.userName?.[0]}</div>
                      </div>
                      <div className="list-item-content">
                        <h4 className="list-name">{user.userName}</h4>
                      </div>
                    </li>
                  </>
                );
              })}
          </ul>
        </div>
      </div>
    );
  }
}
