import React, { Component } from "react";
import { Divider, message } from "antd";
import { Text } from "informed";

import { SubDirectory } from "../../../config.json";
import videohomeImg from "../../Image/videohome.svg";
import { createNewRoom } from "../../../Services/videoCallServices";
export default class VideoHome extends Component {
  constructor(props) {
    super(props);
    this.state = {
      roomName: "",
    };
  }
  startVideo = () => {
    console.log(this.props);
    this.props.history.push({
      pathname: `${SubDirectory}/VideoConsultation`,
      search: "?ConsultationId=61977707bbf6efd64e490b54",
      editState: { preProps: this.props },
    });
  };
  createRoom = async () => {
    if (this.state.roomName.trim()) {
      const response = await createNewRoom(this.state.roomName);
      if (response?.data?.status && response?.data?.roomId) {
        message.success("Room created successfully");
        this.props.history.push({
          // pathname: `/VideoConsultation/?ConsultationId=${this.props.state?.ConsultionId}`,
          pathname: `${SubDirectory}/VideoConsultation`,
          search: `?ConsultationId=${response.data.roomId}`,
        });
      } else {
        message.warning("Try again");
      }
    } else {
      message.warning("Enter Room Name");
    }
  };

  render() {
    return (
      <div className="row  justify-content-center w-100 mt-5">
        <div className="col-lg-7  col-md-7 col-sm-12 pl-5">
          <img src={videohomeImg} width="90%" alt="video" />
        </div>
        <div className="col-lg-4  col-md-4 col-sm-8 d-flex align-item-center ">
          <div className="d-flex  mt-auto mb-auto">
            <div className="row ml-auto  mr-auto p-4">
              <button className="col-12 btn btn-lg btn-primary mt-2" onClick={this.startVideo}>
                Join Test Room
              </button>
              <Divider style={{ borderWidth: 1, borderColor: "#393939" }}>or</Divider>
              <div className=" col-12  mt-3 pb-4">
                <h6>New Room Name</h6>
                <Text
                  placeholder="Enter Room Name"
                  onChange={(e) => {
                    this.setState({ roomName: e.target.value });
                  }}
                  className={`form-control `}
                  field="RoomName"
                />
              </div>
              <button className=" col-12 btn btn-lg btn-primary " onClick={this.createRoom}>
                Create New Room
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
