  defaultErrorCallback = async (error, message) => {

    //some of the possible errors, NotFoundError, SecurityError,PermissionDeniedError will handle here
    if (
      error.indexOf("publishTimeoutError") != -1 &&
      window.roomTimerId != null
    ) {
      clearInterval(window.roomTimerId);
      window.location.reload();
    }

    var errorMessage = JSON.stringify(error);
    if (typeof message != "undefined") {
      errorMessage = message;
    }

    if (error.indexOf("NotFoundError") != -1) {
      errorMessage = "Camera or Mic are not found or not allowed in your device.";
      await this.setState({ isError: true, displayMessage: errorMessage });
    } else if (
      error.indexOf("NotReadableError") !== -1 ||
      error.indexOf("TrackStartError") !== -1
    ) {
      errorMessage = "Camera or Mic is being used by some other process that does not  allow these devices to be read.";
      await this.setState({ isError: true, displayMessage: errorMessage });
    } else if (
      error.indexOf("OverconstrainedError") != -1 ||
      error.indexOf("ConstraintNotSatisfiedError") != -1
    ) {
      errorMessage = "There is no device found that fits your video and audio constraints.";
      await this.setState({ isError: true, displayMessage: errorMessage });
    } else if (
      error.indexOf("NotAllowedError") != -1 ||
      error.indexOf("PermissionDeniedError") != -1
    ) {
      errorMessage = "You are not allowed to access camera and mic.";
      await this.setState({ isError: true, displayMessage: errorMessage });
    } else if (error.indexOf("TypeError") != -1) {
      errorMessage = "Video/Audio is required.";
      // this.openNotification(errorMessage);
      console.log(errorMessage);
    } else if (error.indexOf("UnsecureContext") != -1) {
      errorMessage = "Browser cannot access camera and mic because of unsecure context. Please install SSL and access via https";
      this.openNotification(errorMessage);
    } else if (error.indexOf("WebSocketNotSupported") != -1) {
      errorMessage = "WebSocket not supported in this browser";
      await this.setState({ isError: true, displayMessage: errorMessage });
    } else if (error.indexOf("no_stream_exist") != -1) {
      //TODO: removeRemoteVideo(error.streamId);
    } else if (error.indexOf("data_channel_error") != -1) {
      errorMessage = "There was a error during data channel communication";
      this.openNotification(errorMessage);
    } else if (error.indexOf("ScreenSharePermissionDenied") != -1) {
      errorMessage = "You are not allowed to access screen share";
      this.openNotification(errorMessage);
    } else if (error.indexOf("WebSocketNotConnected") != -1) {
      this.setState({ webSocketModel: true, displayMessage: "Your call is disconnected Please refresh", isError: true });
    }
    else  {
      this.openNotification(errorMessage);
    }
  }
