  checkCameraPerMission = async () => {
    // if (navigator.mediaDevices || navigator.getUserMedia) {
    //   //name: camera
    //   //name: microphone
    //   await navigator.permissions.query({ name: "camera" }).then(({ state }) => {
    //     if (state === "granted") {
    //       console.log("permission granted.....!");

    //       isCameraAccess = true;
    //     } else if (state === "denied") {
    //       console.log("permission denied.........!");
    //       alert("hello");

    //     }
    //   });
    // }
    let isCameraAccess = await navigator.mediaDevices.getUserMedia({ audio: true, video: true }).then(stream => {
      stream.getTracks().forEach(track => track.stop());
      return true;
    }).catch(err => {
      return false;
    });
    return isCameraAccess;
  };

  getlocalVideo = async () => {
    //* get local stream for joining  model

    let videoEle;

    navigator.mediaDevices
      .getUserMedia({ audio: this.state.isMicOn, video: this.state.isCameraOn })
      .then((stream) => {
        console.log("stream", stream);

        this.setState({ LocalStream: stream });
        videoEle = document.querySelector("#modelLocalVideo");
        videoEle.srcObject = stream;
        console.log("video element source", videoEle.srcObject);
        videoEle.play();
      })
      .catch((error) => console.log(error, "media device error"));
  };

  stopLocalStream = () => {
    //* stop the stream, get by  navigator.mediaDevices.getUserMedia()
    if (this.state.LocalStream?.getTracks()) {
      this.state.LocalStream.getTracks().forEach((track) => {
        track.stop();
      });
    }
  };
