import { apiurl, antMediaUrl, staticToken, smskey } from "./../config.json";
import http from "./httpService.js";
import Cookies from "universal-cookie";
const cookies = new Cookies();
export const getCookies = (key) => {
  return cookies.get(key);
};
export const sendInviteSms = (mobileNo, details, link) => {
  // const apiEndPoint = `${apiurl}/sms?MobileNumber=${mobileNo}&Details=${details}&Link=${link}`;
  const apiEndPoint = `${apiurl}/sms`;
  return http.post(`${apiEndPoint}`,{"MobileNumber":`${mobileNo}`,"Details":`${details}`,"Link":`${link}`},{ headers: { "x-api-key": `${smskey}` } });
};

export const sendInviteMail = (details, email, subject) => {
  const apiEndPoint = `${apiurl}/Email?mail_content=${details}&emailId=${email}&subject=${subject}`;
  return http.post(`${apiEndPoint}`);
};

export const getDoctorSearchList = async (name) => {
  const apiEndPoint = `${apiurl}/getDoctorBasedOnName?Count=10&DoctorName=${name}`;
  return http.get(`${apiEndPoint}`);
};

export const createNewRoom = async (roomName) => {
  const apiEndPoint = `${antMediaUrl}/createRoom?roomName=${roomName}`;
  return await http.get(`${apiEndPoint}`, {
    headers: { Authorization: `${staticToken}` },
  });
};

